@import 'color';
@import 'screen';

.left-header {
  float: left;
  height: 4.6em;
}

.right-header {
  float: right;
  height: 4.6em;
  width: 23%;

  .main-nav {
    width: 8em;
  }

  .main-nav > li > ul a li {
    padding: 0.5em 0;
  }
}

.middle-header {
  height: 4.6em;
  float: left;

  button.top-menu {
    padding: 1.6em 1em;
    font-size: 1.1em;
    background-color: $brand-blue;
  }

  .collapse-menu {
    display: inline-flex;
    @media #{$phone-screen} {
      display: block;
      padding: 0;
      position: absolute;
      z-index: 10;
    }

    &.hiddenMenu {
      @media #{$phone-screen} {
        left: -100%;
      }
    }

    button {
      &.top-menu {
        margin-right: 0.2em;
        @media #{$phone-screen} {
          padding: 0.5em 1em;
          display: block;
          width: 7em;
          text-align: left;
        }

        span {
          display: none;
          @media #{$phone-screen} {
            display: inline-block;
          }
        }

        &:hover {
          background-color: $brand-dark-blue;

          span {
            display: inline-block;
          }
        }
      }
    }
  }

  #mobileMenu {
    font-size: 1.1em;
    display: none;
    @media #{$phone-screen} {
      display: block;
    }
  }
}

#client-logged-in {
  display: none;
}

.topMenuBalance {
  visibility: hidden;
}

#login {
  display: none;
  margin: 0.6em 0;
}

.accountSelect {
  float: right;
  margin-right: 1em;

  .account {
    width: 8em;
    float: right;
    font-size: 1em;
  }
}

.btn-primary {
  color: $white !important;
  text-decoration: none !important;
  background: $brand-btn !important;
  border-color: $brand-btn !important;
}

.btn-primary:active,
.btn-primary:hover {
  color: $white !important;
  text-decoration: none !important;
  background-color: $brand-btn-active !important;
  border-color: $brand-btn-active !important;
}
